
const OracleInfo = ({price, vol}) => {
    return (
        <div>
            <h3>Oracle Info</h3>
            <table border="1">
                <tr>
                    <th>BTC/price</th>
                    <th>ETH/price</th>
                    <th>BTC/vol</th>
                    <th>ETH/vol</th>
                </tr>
                <tr>
                    <td>{price.btcPrice}</td>
                    <td>{price.ethPrice}</td>
                    <td>{vol.btcVol}</td>
                    <td>{vol.ethVol}</td>
                </tr>
            </table>
        </div>
    )
}

export default OracleInfo
