import {useState, useEffect} from 'react'
import Web3 from 'web3'
import abis from './utils/abis'

import {TERC20, LTokenOption, PTokenOption, EverlastingOption, IVolatilityOracle, IOracleViewer} from './utils/ABI.json'
import PoolInfo from './components/PoolInfo'
import Symbols from './components/Symbols'
import AccountInfo from './components/AccountInfo'
import Positions from './components/Positions'
import Actions from './components/Actions'
import OracleInfo from "./components/OracleInfo";

function fromWei(value, unit='ether') {
    return Web3.utils.fromWei(value, unit)
}

function toWei(value, unit='ether') {
    return Web3.utils.toWei(value, unit)
}

function fromWeiUSDT(value, unit='Mwei') {
    return Web3.utils.fromWei(value, unit)
}

const getSignature = async ({symbol}) => {
  try {
      let url = `https://oracle2.deri.finance/get_signed_volatility?symbol=VOL-${symbol}`
      let res = await fetch(url);
      let oracle = await res.json();

      return {
          timestamp: oracle.data.timestamp,
          r: oracle.data.r,
          s: oracle.data.s,
          v: oracle.data.v,
          value: oracle.data.volatility}
  } catch (err) {
      console.log(err);
  }
}

const signedPrice = async () => {
        let oracle_btc = await getSignature({symbol:"BTCUSD"})
        let oracle_eth = await getSignature({symbol: "ETHUSD"})
        console.log("BTCvol", oracle_btc.price)
        console.log("ETHvol", oracle_eth.price)
        let signed = [
            {symbolId: 0, timestamp: oracle_btc.timestamp,
                value: oracle_btc.value,
                v: oracle_btc.v,
                r: oracle_btc.r,
                s: oracle_btc.s},
            // {symbolId: 1, timestamp: oracle_btc.timestamp,
            //     price: oracle_btc.price,
            //     v: oracle_btc.v,
            //     r: oracle_btc.r,
            //     s: oracle_btc.s},
            {symbolId: 6, timestamp: oracle_eth.timestamp,
                value: oracle_eth.value,
                v: oracle_eth.v,
                r: oracle_eth.r,
                s: oracle_eth.s},
            ]
        return signed
    }
