function App() {
    const web3 = new Web3(window.ethereum)
    const bToken = new web3.eth.Contract(TERC20,            '0x2ebE70929bC7D930248040f54135dA12f458690C')
    const lToken = new web3.eth.Contract(LTokenOption,       '0x6A4642a84562f565B2ACFd288E373181620252af')
    const pToken = new web3.eth.Contract(PTokenOption,       '0x99c0b6fA472017306F54066291Edb783f16F3Dc1')
    const pool = new web3.eth.Contract(EverlastingOption,  '0x041FC773fD97f878429d6F40b5B3420ce8e92672')
    const priceBTC = new web3.eth.Contract(IOracleViewer,  '0x18C036Ee25E205c224bD78f10aaf78715a2B6Ff1')
    const priceETH = new web3.eth.Contract(IOracleViewer,  '0x073C99954e1cf5eb6f4Ef6f1B7FF21ACf735Ee6A')
    const volBTC = new web3.eth.Contract(IVolatilityOracle,  '0x7A4701A1A93BB7692351aEBcD4F5Fab1d4377BBc')
    const volETH = new web3.eth.Contract(IVolatilityOracle,  '0xF03fDB7193826E11310DE6e297826c4E29E898B9')

    const [data, setData] = useState({
        account: '',
        poolinfo: {},
        symbols: [[]],
        accountInfo: {},
        positions: [],
        price: {},
        vol: {}
    })

    useEffect(() => {
        update()
    }, [])

    const update = async () => {
        const account = await getAccount()
        const poolinfo = await getPoolInfo()
        const symbols = await getSymbols(poolinfo.activeSymbolIds)
        const accountInfo = await getAccountInfo(account)
        const positions = await getPositions(account, poolinfo.activeSymbolIds)
        const price = await getOraclePrice()
        const vol = await getOracleVol()
        setData({account, poolinfo, symbols, accountInfo, positions, price, vol})
    }

    const getOraclePrice = async () => {
        const btcPrice = fromWei(await priceBTC.methods.getPrice().call())
        const ethPrice = fromWei(await priceETH.methods.getPrice().call())
        return {btcPrice, ethPrice}
    }

    const getOracleVol = async () => {
        console.log('start')
        const btcVol = fromWei(await volBTC.methods.getVolatility().call())
        const ethVol = fromWei(await volETH.methods.getVolatility().call())
        console.log('vol', btcVol, ethVol)
        return {btcVol, ethVol}
        // return {}
    }

    const getAccount = async () => {
        return Web3.utils.toChecksumAddress((await window.ethereum.request({method: 'eth_requestAccounts'}))[0])
    }

    const getPoolInfo = async () => {
        const balance = fromWei(await bToken.methods.balanceOf(pool.options.address).call())
        const lTokenTotalSupply = fromWei(await lToken.methods.totalSupply().call())
        const pTokenTotalSupply = await pToken.methods.totalSupply().call()
        const activeSymbolIds = await pToken.methods.getActiveSymbolIds().call()
        const numPositionHolders = await Promise.all(activeSymbolIds.map(async (id) => {return await pToken.methods.getNumPositionHolders(id).call()}))
        const liquidity = fromWei((await pool.methods.getPoolStateValues().call())[0])
        const protocolFeeAccrued = fromWei((await pool.methods.getPoolStateValues().call())[2])
        return {
            balance,
            lTokenTotalSupply,  
            pTokenTotalSupply,
            activeSymbolIds,
            numPositionHolders,
            liquidity,
            protocolFeeAccrued
        }
    }


// "symbolId",
//         "symbol",
//         "oracleAddress",
//         "volatilityAddress",
//         "isCall",
//         "strikePrice",
//         "multiplier",
//         "feeRatio",
//         "alpha",
//         "tradersNetVolume",
//         "tradersNetCost",
//         "cumulativePremiumFundingRate",



    const getSymbols = async (activeSymbolIds) => {
        return await Promise.all(activeSymbolIds.map(async (id) => {
            let s = await pool.methods.getSymbol(id).call()
            return [
                s[0],
                s[1],
                s[2],
                s[3],
                s[4]==true? "True" : "False",
                fromWei(s[5]),
                fromWei(s[6]),
                fromWei(s[7]),
                fromWei(s[8]),
                fromWei(s[9]),
                fromWei(s[10]),
                fromWei(s[11]),

            ]
        }))
    }

    const getAccountInfo = async (account) => {
        const balance = fromWei(await bToken.methods.balanceOf(account).call())
        const lTokenBalance = fromWei(await lToken.methods.balanceOf(account).call())
        const margin = fromWei(await pToken.methods.getMargin(account).call())
        return {
            balance,
            lTokenBalance,
            margin
        }
    }

    const getPositions = async (account, activeSymbolIds) => {
        return await Promise.all(activeSymbolIds.map(async (id) => {
            let p = await pToken.methods.getPosition(account, id).call()
            return [
                fromWei(p[0]),
                fromWei(p[1]),
                fromWei(p[2])
            ]
        }))
    }

    const onMint = async () => {
        await bToken.methods.mint(data.account, "100000000000000000000000").send({from: data.account})
        await update()
    }

    const onApprove = async () => {
        const max = '0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff'
        await bToken.methods.approve(pool.options.address, max).send({from: data.account})
    }



    const onAddLiquidity = async (value) => {
        await pool.methods.addLiquidity(toWei(value), []).send({from: data.account})
        await update()
    }

    const onAddLiquidityUpdate = async (value) => {
        let signed = await signedPrice()
        await pool.methods.addLiquidity(toWei(value), signed).send({from: data.account})
    }


    const onRemoveLiquidity = async (value) => {
        let signed = await signedPrice()
        await pool.methods.removeLiquidity(toWei(value), signed).send({from: data.account})
        await update()
    }

    const onAddMargin = async (value) => {
        await pool.methods.addMargin(toWei(value)).send({from: data.account})
        await update()
    }

    const onRemoveMargin = async (value) => {
        let signed = await signedPrice()
        await pool.methods.removeMargin(toWei(value), signed).send({from: data.account})
        await update()
    }

    const onTrade = async (symbolId, volume) => {
        let signed = await signedPrice()
        await pool.methods.trade(symbolId, toWei(volume), signed).send({from: data.account})
        await update()
    }

    const onLiquidate = async (address) => {
        let signed = await signedPrice()
        await pool.methods.liquidate(address, signed).send({from: data.account})
        await update()
    }

    return (
        <div className="App">
            <h1>DeriV2 EverlastingOption BSC Testnet</h1>
            <PoolInfo poolinfo={data.poolinfo} />
            <Symbols symbols={data.symbols} />
            <AccountInfo accountInfo={data.accountInfo} />
            <OracleInfo price={data.price} vol={data.vol} />
            <Positions symbols={data.symbols} positions={data.positions} />
            <Actions
                onMint={onMint}
                onApprove={onApprove}
                onAddLiquidity={onAddLiquidityUpdate}
                onRemoveLiquidity={onRemoveLiquidity}
                onAddMargin={onAddMargin}
                onRemoveMargin={onRemoveMargin}
                onTrade={onTrade}
                onLiquidate={onLiquidate}
            />
        </div>
    );
}

export default App;
